import React from 'react';

class SingleData extends React.Component{
  state = {
    cont: false
  }
  render(){
    const data = this.props.data;
    return (
    <div className="col-md-12">
      <div className="card mb-2" onClick={() => {
        this.setState({
          cont: !this.state.cont
        })
      }}>
        <div className="card-header">{data.name}

          <span className="float-right p-2">
            {this.state.cont ? 
            <i className="fa fa-arrow-up float-right"></i> : <i className="fa fa-arrow-down float-right"></i>}
          </span>
          
          {data.status === 'pending' ? 
          <span className="float-right text-warning">{data.status}</span>
          :
          <span className="float-right text-success">{data.status}</span>
          }
        </div>
        {this.state.cont && 
          <div className="card-body">
            <div className="mb-2">{data.description}</div>
            <strong  className="mt-4">Created At: </strong><small>{data.createdAt.substring(0, data.createdAt.indexOf('T'))}</small>
          </div>
        }
      </div> 
    </div>
    )
  }
}

export default SingleData;