import React from 'react';
import Axios from 'axios';
import { connect } from 'react-redux';
import Row from '../Row';
import Card from '../Card';
import conf from '../../config';
import Loader from '../Loader';
import { MyImage, deleteContent, DeleteImage } from '../../Actions/content';
import SingleData from './SingleData'

class UserImages extends React.Component{
  state = {
    data: false
  }
  componentDidMount(){
    Axios.get(`${conf.server}/api/order/image`, {
      headers: {
        'authorization': localStorage.getItem('auth')
      }
    }).then((res) => {
      this.setState({
        data: res.data
      })
      // this.props.MyImage(res.data);      
    }).catch((e) => {
      console.log(e);
      this.setState({
        data: undefined
      })
    })
  }

  // DeleteImage = (id) => {
  //   Axios.delete(`${conf.server}/api/content/${id}`, {
  //     headers: {
  //       'authorization': localStorage.getItem('auth')
  //     }
  //   }).then((res) => {
  //     this.props.DeleteImage(id);
  //   }).catch((e) => {
  //     console.log(e)
  //   })
  // }

  render(){
    const data = this.state.data
    return (
      <div className="col-md-9">
      {data &&
        <Row>
          {data.slice(0, 12).map((image) => {
            return (
              <SingleData data={image} key={image._id}/>
            )
          })}
        </Row>
      }
      {!this.state.data && this.state.data != undefined && <Loader/>}
      </div>
    )
  }
}

