import React from 'react';
import Container from '../Components/Container';
import Row from '../Components/Row';
import Axios from 'axios';
import conf from '../config';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

class CustomOrder extends React.Component{
  state = {
    name: '',
    email: '',
    description: '',
    msg: false
  }

  sendOrder = (e) => {
    e.preventDefault();
    Axios.post(`${conf.server}/api/order/create`, {
      name: this.refs.name.value,
      email: this.refs.email.value,
      description: this.refs.description.value,
      contentType: this.refs.c_type.value
    }, {
      headers: {
        'Authorization': localStorage.getItem('auth')
      }
    }).then((res) => {
      this.setState({
        msg: true
      })
    }).catch((e) => {
      this.setState({
        msg: undefined
      })
    })
  }

  render(){
    return(
      <section id="order" className="">
        <Container>
          <div className="col-md-8 mx-auto">
            {this.props.isAuthenticated ? 
            <form className="form-group " onSubmit={this.sendOrder}>
              <h1 className="text-center text-pri pb-5">Order For A Custom Video/Image</h1>
              <Row>
                <div className="col-md-6 mb-4">
                  <input className="form-control" ref="name" placeholder="Your Full Name" type="text"/>
                </div>
                <div className="col-md-6 mb-4">
                  <input className="form-control" ref="email" placeholder="Email" type="email"/>
                </div>
                <div className="col-md-12 mb-4">
                  <select ref="c_type" required className="form-control">
                    <option value="">Select Content Type</option>
                    <option value="Video">Video</option>
                    <option value="Image">Image</option>
                  </select>
                </div>
                <div className="col-md-12 mb-4">
                  <textarea ref="description" rows={10} className="form-control" placeholder="Please Describe Your Content..."></textarea>
                </div>
              </Row>
              <div className="text-center py-3 ">
                <button type="submit" className="btn btn-pri">Send to Us</button>
              </div>
              {this.state.msg && <div className="col-md-6 offset-3 alert alert-success text-center">Your order submitted successfully to us. We will get in touch shortyly</div>}
              {this.state.msg === undefined && <div className="col-md-6 offset-3  alert alert-warning text-center">Can not send. Please Try Again Latter</div>}
            </form>
            : <p>Please <Link to="/login">Login</Link> To Order A Custom Video/Image</p>}
          </div>
        </Container>
      </section>
    )
  }
}
const mapStateToProps = (state) => ({
  isAuthenticated: !!localStorage.getItem('auth')
})

export default connect(mapStateToProps)(CustomOrder)
