import React from 'react';
import Sidebar from '../Components/Sidebar';
import Grid from '../Components/Grids/Grid';
import SearchBar from '../Components/SearchBar';
import { setSingleContent, getSingle, setContent } from '../Actions/content';
import Axios from 'axios';
import { connect } from 'react-redux';
import SingleContent from '../Components/Content/Single.Content';
import conf from '../config';
import Loader from '../Components/Loader'
import Container from '../Components/Container';
import PopularContent from '../Components/Content/PopularContent';
import Row from '../Components/Row';

class Single extends React.Component{
  state = {
    single: false, 
    all: false
  }

  componentDidMount(){
    Axios.get(`${conf.server}/api/content/${this.props.match.params.id}`).then((res) => {
        this.props.setOneContent(res.data);
        this.setState({
            single: res.data
        })
        this.StateUpdate({ views: this.state.single.views+1 });
    }).catch((e) => {
        this.setState({
            single: false
        })
        console.log('Error getting message', e);
        throw e;
    })

    Axios.get(`${conf.server}/api/content/`).then((res) => {
        this.props.setContent(res.data);
        this.setState({
            all: res.data
        })
        this.StateUpdate({ views: this.state.single.views+1 });
    }).catch((e) => {
        this.setState({
            single: false
        })
        console.log('Error getting message', e);
        throw e;
    })
  }

  // componentWillReceiveProps(){
  //   this.setState({
  //     single: false
  //   })
  //   Axios.get(`${conf.server}/api/content/${this.props.match.params.id}`).then((res) => {
  //       this.props.setOneContent(res.data);
  //       this.setState({
  //           single: res.data
  //       })
  //       this.StateUpdate({ views: this.state.single.views+1 });
  //   }).catch((e) => {
  //       this.setState({
  //           single: false
  //       })
  //       console.log('Error getting message', e);
  //       throw e;
  //   })
  // }


  StateUpdate = (data) => {
    Axios({
      method: 'put',
      url: `${conf.server}/api/content/public/${this.props.match.params.id}`,
      data,
      headers: {
          'Content-Type': 'application/json'
      }
    }).then((res) => {
      this.props.setOneContent(res.data);

      this.setState({
        single: res.data
      })
    }).catch(e => {
      console.log(e)
    })
  }

  
  render(){
    const popular = this.props.allContent && this.props.allContent.sort((a, b) => {
      return b.views - a.views
    }).slice(0, 3);
    return(
      <section className="py-5 bg-light" id="single">
        <div className="content">
          <Container>
            {this.props.single ?
            <Row>
              <SingleContent
                single={this.props.single}
                StateUpdate={this.StateUpdate}
                shareLink={this.props.location.pathname}
              />
              {popular && <PopularContent data={popular}/>}
            </Row>
              :
              <Loader/>
            }
          </Container>
        </div>
      </section>
      )
    }
  }

const mapStateToProps = (state) => ({
  allContent: state.content.all,
  single: state.content.single
})

const mapDispatchToProps = (dispatch, props, getState) => ({
  setOneContent: (data) => dispatch(setSingleContent(data)),
  setSingleRedux: () => dispatch(getSingle(props.match.params.id)),
  setContent: (body) => dispatch(setContent(body))  
}) 

export default connect(mapStateToProps, mapDispatchToProps)(Single)
