import React from 'react';
import Axios from 'axios';
import { connect } from 'react-redux';
import Row from '../Row';
import Loader from '../Loader';
import conf from '../../config';
import Card from '../Card';
import { MyVideo, DeleteVideo } from '../../Actions/content';
import SingleData from '../User/SingleData';

class Orders extends React.Component{
  state = {
    orders: false
  }

  componentDidMount() {
    Axios.get(`${conf.server}/api/order/all`, {
      headers: {
        'authorization': localStorage.getItem('auth')
      }
    }).then((res) => {
      this.setState({
        orders: res.data
      })
    }).catch((e) => {
      this.setState({
        orders: undefined
      })
      console.log(e)
    })
  }
  
  render(){
    return (
      <div className="col-md-9">
        {this.state.orders ?
        <Row>
          {this.state.orders.slice(0).reverse().map((order) => {
            return (
              <SingleData data={order} key={order._id}/>
              // <div className="col-md-12">
              //   <div className="card mb-2" onClick={() => this.props.history.push('/myaccount/orders/asdfdsf')}>
              //     <div className="card-header">{order.name}
              //       {order.status === 'pending' ? 
              //       <span className="float-right text-warning">{order.status}</span>
              //       :
              //       <span className="float-right text-success">{order.status}</span>
              //       }
              //     </div>
              //   </div> 
              // </div>
            )
          })}
        </Row> 
        :
        this.state.orders != undefined && <Loader/>}
        {this.state.orders === undefined && <p>Nothing Found</p>}
      </div>
    )
  }
}
export default (Orders);